				"html": "<p>The <code>logical or</code> operator (<code>||</code>) returns <code>true</code> if either of the <code>operands</code> is <code>true</code>. Otherwise, it returns <code>false</code>.</p>\n<p>The pattern below should look familiar from prior Challenges:</p>\n<pre><code>if (num > 10) {\n  return \"No\";\n}\nif (num &#x3C; 5) {\n    return \"No\";\n}\nreturn \"Yes\";\n</code></pre>\n<p>will return \"Yes\" only if <code>num</code> is between <code>5</code> and <code>10</code> (5 and 10 included). The same logic can be written as:</p>\n<pre><code>if (num > 10 || num &#x3C; 5) {\n  return \"No\";\n}\nreturn \"Yes\";\n</code></pre>",
